.slider-container {
  position: relative;
  width: 100%;
  max-width: 1200px;
  height: 400px;
  margin: 0 auto; /* ✅ 중앙 정렬 */
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

.slider-wrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider-track {
  display: flex;
  height: 100%;
  transition: transform 1s ease-in-out;
}

.slider-item {
  flex: 0 0 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.poster-image {
  max-height: 100%;
  max-width: 100%;
  height: auto;
  width: auto;
  object-fit: contain;
  display: block;
  margin: 0 auto;
}


.slide-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: white;
  border: none;
  font-size: 2rem;
  z-index: 2;
  cursor: pointer;
  opacity: 0.5;
  transition: opacity 0.3s;
}
.slide-btn:hover {
  opacity: 1;
}
.slide-btn.left {
  left: 10px;
}
.slide-btn.right {
  right: 10px;
}
.slider-item {
  flex: 0 0 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background-color: #fff; /* 여백 배경 흰색 */
}

